<template>
    <div id="top">
        <a-layout>
            <a-layout-content>
                <a-row>
                    <a-col :span="8">
                        <h2>日志内容如下</h2>
                    </a-col>
                </a-row>
                <hr />
                <div>
                    <pre v-if="type !== 'instance'">
{{ log }}
                </pre>
                    <pre v-else>
{{ instanceLog }}
                </pre>
                </div>
                <div ref="down"></div>
            </a-layout-content>
        </a-layout>
    </div>
</template>

<script>
    export default {
        data: function () {
            return {
                log: "没有可用日志",
                type: null,
                timer: "",
            }
        },
        computed: {
            instanceLog() {
                return this.$store.state.lastInstanceLogs;
            }
        },
        methods: {
            back() {
                this.$router.go(-1);
            },
            refresh() {
                this.$router.go();
            }
        },
        created() {
            this.$store.dispatch("CurrentUserAction");

            //这里需要通过_self 跳转过来。在新窗口打开的store状态不对
            let id = this.$route.params.id;
            this. type = this.$route.params.type;
            if (this.type == "instance") {
                this.$store.dispatch("GetInstanceLogs", id)
                return
            }

            const histories = this.$store.state.histories;
            const h = histories.find(x => x.id == id)
            if (this.type === "stderr") {
                this.log = h.stderr;
            }
            if (this.type === "stdout") {
                this.log = h.stdout;
            }
        },
        mounted() {
            setTimeout(
                () => {
                    this.$refs.down.scrollIntoView({ behavior: 'smooth' });
                }, 500)
            this.timer = setInterval(this.refresh, 15000);
        },
        beforeDestroy() {
            clearInterval(this.timer);
        }
    }
</script>

<style scoped>
    #top .ant-layout-content {
        padding: 0 50px;
        padding-bottom: 60px;
        background-color: white;
    }

    #top .ant-layout-footer {
        background: white;
        text-align: center;
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 60px;
        clear: both;
    }


</style>
