<template>
    <div id="top">
        <a-layout>
            <a-layout-header class="header" :style="{ position: 'fixed', zIndex: 1, width: '100%' }">
                <div class="logo">
                    欢迎：{{ currentUser }}
                </div>
                <a-menu theme="dark" mode="horizontal" :style="{ lineHeight: '64px' }"
                        @click="onMenuClick">
                    <a-menu-item key="create">
                        <a-icon type="fork" />
                        <span>创建任务</span>
                    </a-menu-item>
                    <a-menu-item key="list">
                        <a-icon type="dashboard" />
                        <span> 任务列表</span>
                    </a-menu-item>
                    <a-menu-item key="detail" class="ant-menu-item-active">
                        <a-icon type="edit" />
                        <span>任务详情</span>
                    </a-menu-item>
                    <a-menu-item key="listinstance">
                        <a-icon type="history" />
                        <span>实例列表</span>
                    </a-menu-item>
                </a-menu>
            </a-layout-header>
            <a-layout-content>
                <a-modal v-model="dlgShow" title="设置触发器" @ok="onDlgOK">
                    <a-form-model :model="form">
                        <a-form-item label="调度策略">
                            <a-radio-group v-model="form.trigger">
                                <a-radio-button value="cron">周期</a-radio-button>
                                <a-radio-button value="interval">间隔</a-radio-button>
                                <a-radio-button value="date">定期</a-radio-button>
                            </a-radio-group>
                        </a-form-item>
                        <a-form-item v-if="form.trigger == 'interval'" label="调度间隔 秒">
                            <a-input v-model="form.interval_trigger_args" placeholder="请输入调度间隔" />
                        </a-form-item>
                        <a-form-item v-if="form.trigger == 'date'" label="选择时间">
                            <a-date-picker v-model="form.date_trigger_args" show-time valueFormat="yyyy-MM-DD HH:mm:ss"
                                           placeholder="选择固定的时间"></a-date-picker>
                        </a-form-item>
                        <a-form-item v-if="this.form.trigger == 'cron'" label="选择月份">
                            <a-select v-model="form.cron_trigger_args.month" mode="multiple" placeholder="默认每月">
                                <a-select-option v-for="(item, index) in month_list" :key="index" :value="index + 1">{{ item }}</a-select-option>
                            </a-select>
                        </a-form-item>
                        <a-form-item v-if="this.form.trigger == 'cron'" label="输入日期">
                            <a-select v-model="form.cron_trigger_args.day" mode="tags"
                                      placeholder="默认每天">
                                <a-select-option value="last">每月最后一天</a-select-option>
                                <a-select-option disabled value="s1">示例：*/2 每2天</a-select-option>
                                <a-select-option disabled value="s2">示例：1-7 1号到7号</a-select-option>
                                <a-select-option disabled value="s3">示例：1,7 1号和7号</a-select-option>
                                <a-select-option disabled value="s4">示例：1-7/2 1号到7号每2天</a-select-option>
                                <a-select-option disabled value="s5">说明：取值范围(1-31)，输入多个值，则之间为或关系</a-select-option>
                            </a-select>
                        </a-form-item>
                        <a-form-item v-if="this.form.trigger == 'cron'" label="输入小时">
                            <a-select v-model="form.cron_trigger_args.hour" mode="tags"
                                      placeholder="默认每小时">
                                <a-select-option value="s1" disabled>示例：*/2  每2小时</a-select-option>
                                <a-select-option value="s2" disabled>示例：8-20 早8点到晚8点</a-select-option>
                                <a-select-option value="s3" disabled>示例：8,20 早8点和晚8点</a-select-option>
                                <a-select-option value="s4" disabled>示例：8-20/2 早8点到晚8点每2小时</a-select-option>
                                <a-select-option value="s5" disabled>说明：取值范围(0-23)，输入多个值，则之间为或关系</a-select-option>
                            </a-select>
                        </a-form-item>
                        <a-form-item v-if="this.form.trigger == 'cron'" label="输入分钟">
                            <a-select v-model="form.cron_trigger_args.minute" mode="tags"
                                      placeholder="默认每分钟">
                                <a-select-option value="s1" disabled>示例：*/5 每5分钟</a-select-option>
                                <a-select-option value="s2" disabled>示例：0-30 整点到第30分钟</a-select-option>
                                <a-select-option value="s3" disabled>示例：0,30 整点和第30分钟</a-select-option>
                                <a-select-option value="s4" disabled>示例：0-30/5 前半个小时每5分钟</a-select-option>
                                <a-select-option value="s5" disabled>说明：取值范围(0-59)，输入多个值，则之间为或关系</a-select-option>
                            </a-select>
                        </a-form-item>
                        <a-form-item v-if="this.form.trigger == 'cron'" label="输入每周">
                            <a-select v-model="form.cron_trigger_args.day_of_week" mode="tags"
                                      placeholder="默认周一到周日都执行">
                                <a-select-option value="s1" disabled>示例：5 每周五执行</a-select-option>
                                <a-select-option value="s2" disabled>示例：1-5 周一到周五执行</a-select-option>
                                <a-select-option value="s3" disabled>示例：1,2,3 周一周二周三执行</a-select-option>
                                <a-select-option value="s4" disabled>说明：取值范围(0-6)，0代表周日，6代表周六</a-select-option>
                            </a-select>
                        </a-form-item>
                        <a-form-item v-if="this.form.trigger == 'cron'" label="生效时间">
                            <a-range-picker show-time v-model="form.cron_trigger_args.range" type="daterange" valueFormat="yyyy-MM-DD HH:mm:ss" ></a-range-picker>
                        </a-form-item>
                    </a-form-model>
                </a-modal>

                <table class="table" v-if="ready">
                    <thead>
                        <tr>
                            <th>属性</th>
                            <th>值</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>任务ID</td>
                            <td>{{ task.id }}</td>
                        </tr>
                        <tr>
                            <td>任务名字</td>
                            <td>{{ task.name }}</td>
                        </tr>
                        <tr>
                            <td>用户</td>
                            <td>{{ task.user }}</td>
                        </tr>
                        <tr>
                            <td>描述</td>
                            <td>{{ task.desc }}</td>
                        </tr>
                        <tr>
                            <td>调度周期</td>
                            <td>
                                <div class="form-group">
                                    <label for="schdTypes">调度类型</label>
                                    <select disabled id="schdTypes" class="form-control" v-model="selType">
                                        <option v-for="t in schdTypes" :key="t">
                                            {{ t }}
                                        </option>
                                    </select>
                                    <a :href="doc" target="_blank" v-show="selType!=''">参数设置参考文档</a>
                                </div>
                                <div class="form-group">
                                    <label for="schdArgs">调度参数</label>
                                    <input type="text" class="form-control" v-model="$v.selArgs.$model" disabled />
                                    <validation-error v-bind:validation="$v.selArgs" />
                                </div>
                                <div class="form-group mt-0">
                                    <button class="btn btn-outline-warning" @click="setTaskTrigger">设置调度</button>
                                    <a-switch checked-children="开" un-checked-children="关" :checked="checked" @change="switchTrigger" />
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <td>命令</td>
                            <td>
                                <div class="form-group">
                                    <textarea class="form-control" rows="10" v-model="task.cmd">
                            </textarea>
                                    <button class="btn btn-outline-info" @click="runCmd">直接运行命令</button>
                                    <button class="btn btn-outline-warning" @click="updateCmd">更改运行命令</button>
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <td>运行历史</td>
                            <td>
                                <table class="table table-bordered " v-if="histories">
                                    <thead>
                                        <tr>
                                            <td>创建时间</td>
                                            <td>耗时 s</td>
                                            <td>退出代码</td>
                                            <td>标准输出</td>
                                            <td>错误输出</td>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="h in histories" v-bind:key="h.id">
                                            <td>{{ h.created  }}</td>
                                            <td>{{ h.taken }}</td>
                                            <td>{{ h.code }}</td>
                                            <td>
                                                <router-link target="_self" v-bind:to="'/log/' + h.id + '/stdout' ">stdout</router-link>
                                            </td>
                                            <td>
                                                <router-link target="_self" v-bind:to="'/log/' + h.id + '/stderr' ">stderr</router-link>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                                <span v-else>没有运行历史</span>
                            </td>
                        </tr>
                        <tr>
                            <td>其他操作</td>
                            <td>
                                <button class="btn btn-danger" @click="deleteTask">删除本任务</button>

                            </td>
                        </tr>
                    </tbody>
                </table>
            </a-layout-content>
        </a-layout>
    </div>
</template>

<script>
    import { required } from "vuelidate/lib/validators";
    import ValidationError from "./ValidationError";

    const month_list = ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月'];

    export default {
        components: { ValidationError },
        validations: {
            selArgs: {
                required
            }
        },
        data: function () {
            return {
                task: null,
                selType: "",
                selArgs: "",
                schdTypes: [
                    "cron",
                    "interval",
                    "date",
                ],
                typeDocs: {
                    "cron": "https://apscheduler.readthedocs.io/en/stable/modules/triggers/cron.html#module-apscheduler.triggers.cron",
                    "interval": "https://apscheduler.readthedocs.io/en/stable/modules/triggers/interval.html#module-apscheduler.triggers.interval",
                    "date": "https://apscheduler.readthedocs.io/en/stable/modules/triggers/date.html#module-apscheduler.triggers.date",
                },
                ready: false,
                checked: false,
                dlgShow: false,
                month_list: month_list,
                form: {
                    trigger: "",
                    interval_trigger_args: "",
                    date_trigger_args: "",
                    cron_trigger_args: { month: [], day: [], hour: [], minute: [], day_of_week: [], range: [] },
                },
                
            }
        },
        computed: {
            doc() {
                return this.typeDocs[this.selType];
            },
            histories() {
                return this.$store.state.histories;
            },
            currentUser: function () {
                return this.$store.state.username;
            }
        },
        created() {
            this.$store.dispatch("CurrentUserAction");

            let taskId = this.$route.params.id;

            let t = this.$store.state.tasks.find(x => x.id == taskId);
            if (t == undefined) {
                this.$store.watch(state => state.actionTaskOrErr,
                    (t) => {
                        if (typeof (t) === 'string') {
                            this.$message.error('加载任务详情错误: ' + t, 3);
                        } else {
                            this.task = {};
                            Object.assign(this.task, t);
                            if (this.task.trigger) {
                                this.selType = this.task.trigger;
                            }
                            if (this.task.trigger_args) {
                                this.selArgs = this.task.trigger_args;
                            }
                            this.ready = true;
                            this.checked = this.task.enabled;
                            this.$store.dispatch("GetTaskHistoryAction", taskId)
                        }
                    }
                );

                this.$store.dispatch("GetTaskAction", taskId);
            } else { 
                this.task = {};
                Object.assign(this.task, t);
                if (this.task.trigger) {
                    this.selType = this.task.trigger;
                }
                if (this.task.trigger_args) {
                    this.selArgs = this.task.trigger_args;
                }
                this.ready = true;
                this.checked = this.task.enabled;

                this.$store.dispatch("GetTaskHistoryAction", taskId)
            }
        },

        methods: {
            deleteTask() {
                const store = this.$store;
                const router = this.$router;
                const task = this.task;

                this.$confirm({
                    title: `删除任务`,
                    content: '确定彻底删除任务吗',
                    onOk() {
                        store.dispatch("DeleteTaskAction", task);
                        router.replace("/list")
                    },
                    onCancel() { },
                });

                
            },
            switchTrigger(check) {
                this.checked = check;
                this.task.enabled = check;
                const request = {
                    id: this.task.id,
                    checked: check,
                };
                this.$store.dispatch("SwitchTriigerAction", request);
            },
            runCmd() {
                let unwatch = this.$store.watch(state => state.runSetMsg,
                    (msg) => {
                        const arr = msg.split("|");
                        const message = arr[1];
                        if (message === "") {
                            this.$message.info("命令提交成功", 3);
                        } else {
                            this.$message.error("命令提交失败: " + message, 3);
                        }
                        unwatch();
                    }
                );

                this.$store.dispatch("RunCmdAction", this.task.cmd);
            },
            updateCmd() {
                let unwatch = this.$store.watch(state => state.taskSetMsg,
                    (msg) => {
                        const arr = msg.split("|");
                        const message = arr[1];
                        if (message === "") {
                            this.$message.info("cmd 设置成功", 3);
                        } else {
                            this.$message.error("cmd 设置失败: " + message, 3);
                        }
                        unwatch();
                    }
                );

                this.$store.dispatch("UpdateTaskAction", this.task);
            },
            onDlgOK() {
                if (this.form.trigger === '') {
                    this.$message.error("please select a kind of trigger type", 3);
                    return;
                }

                let args = "";

                if (this.form.trigger === 'interval') {
                    args = this.form.interval_trigger_args.trim();
                    if (args === '') {
                        this.$message.error("interval need args", 3);
                        return;
                    }
                    let num = parseInt(args, 10);
                    if (isNaN(num)) {
                        this.$message.error("interval args must be integer", 3);
                        return;
                    }
                    if (num < 1) {
                        this.$message.error("interval args must be positive(number) second", 3);
                        return;
                    }

                    let numStr = num + '';
                    if (numStr.length != args.length) {
                        this.$message.error("interval args must be integer", 3);
                        return;
                    }
                }
                if (this.form.trigger === 'date') {
                    args = this.form.date_trigger_args;
                    if (args === '') {
                        this.$message.error("date need args", 3);
                        return;
                    }
                }

                if (this.form.trigger == 'cron') {
                    args = ";"; //year
                    args += this.handleCronArgs(this.form.cron_trigger_args.month); //month
                    args += this.handleCronArgs(this.form.cron_trigger_args.day);  //day
                    args += ';';  //week
                    args += this.handleCronArgs(this.form.cron_trigger_args.day_of_week);  //day_of_week
                    args += this.handleCronArgs(this.form.cron_trigger_args.hour);  //hour
                    args += this.handleCronArgs(this.form.cron_trigger_args.minute);  //minute
                    args += ';';  //second
                    if (this.form.cron_trigger_args.range && this.form.cron_trigger_args.range.length) {
                        args += this.form.cron_trigger_args.range[0] + ';'; //start_date
                        args += this.form.cron_trigger_args.range[1]    //end_date
                    } else {
                        args += ';'
                    }
                }
                this.selType = this.form.trigger;
                this.selArgs = args;
                this.dlgShow = false;

                let req = {
                    id: this.task.id,
                    trigger: this.selType,
                    trigger_args: this.selArgs,
                };

                let unwatch = this.$store.watch(state => state.triggerSetMsg,
                    (msg) => {
                        const arr = msg.split("|");
                        const message = arr[1];
                        if (message === "") {
                            this.$message.info("trigger 设置成功", 3);
                        } else {
                            this.$message.error("trigger 设置失败: " + message, 3);
                        }
                        unwatch();
                    }
                );

                this.$store.dispatch("SetTaskTriggerAction", req);
            },
            setTaskTrigger() {
                this.dlgShow = true;
            },
            onMenuClick(e) {
                if (e.key != "detail") {
                    this.$router.push("/" + e.key);
                }
            },
            handleCronArgs(args) {
                if (args.length) {
                    return args.join(',') + ';'
                } else {
                    return ';'
                }
            },
        }
    }
</script>

<style scoped>

    #top .ant-layout-header {
        background: white;
    }

    #top .ant-layout-content {
        padding: 0 50px;
        margin-top: 64px;
        padding-bottom: 60px;
        background-color: white;
    }

    #top .ant-layout-footer {
        background: white;
        text-align: center;
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 60px;
        clear: both;
    }

    #top .logo {
        width: 240px;
        height: 31px;
        float: left;
        color: white
    }
</style>
