<template>
    <div id="top">
        <a-layout>
            <a-layout-header class="header" :style="{ position: 'fixed', zIndex: 1, width: '100%' }">
                <div class="logo">
                    欢迎：{{ currentUser }}
                </div>
                <a-menu theme="dark" mode="horizontal" :style="{ lineHeight: '64px' }"
                        @click="onMenuClick">
                    <a-menu-item key="create">
                        <a-icon type="fork" />
                        <span>创建任务</span>
                    </a-menu-item>
                    <a-menu-item key="list">
                        <a-icon type="dashboard" />
                        <span> 任务列表</span>
                    </a-menu-item>
                    <a-menu-item key="detail" disabled>
                        <a-icon type="edit" />
                        <span>任务详情</span>
                    </a-menu-item>
                    <a-menu-item key="listinstance" class="ant-menu-item-active">
                        <a-icon type="history" />
                        <span>实例列表</span>
                    </a-menu-item>
                </a-menu>
            </a-layout-header>
            <a-layout-content>
                <div class="row">
                    <div class="col-1">
                    </div>
                    <div class="col-3">
                        <input class="form-control" v-model="search" type="text" placeholder="调度名字过滤" />
                    </div>
                    <div class="input-group-append">
                        <button class="btn btn-outline-info" @click="advList" >搜索</button>
                    </div>
                </div>
                <a-table id="tasktable" rowKey="id"
                         :columns="columns"
                         :dataSource="instances"
                         :pagination="pagination"
                         @change="onTableChange">
                    <span slot="detail" slot-scope="text, record">
                        <router-link target="_blank" v-bind:to="'/log/' + record.schedule_name + '/instance/'">日志</router-link>
                        
                        <button v-if="record.status === 'RUNNING' || record.status === 'PENDING'" @click="onKill(record)" class="btn btn-sm btn-link">击杀</button>
                    </span>

                </a-table>
            </a-layout-content>
            <!--
            <a-layout-footer>
                Copyright  2020-2021 CooTek Big Data Platform
            </a-layout-footer>
            -->
        </a-layout>
    </div>
</template>

<script>

    export default {
        data: function () {
            return {
                search: "",
                columns: [
                    {
                        title: "实例类型",
                        dataIndex: "type",
                    }, {
                        title: "任务名字",
                        dataIndex: "name",
                    }, {
                        title: "调度名字",
                        dataIndex: "schedule_name",
                    },{
                        title: "状态",
                        dataIndex: "status",
                    }, {
                        title: "调度作业id",
                        dataIndex: "job_id",
                    }, {
                        title: "创建用户",
                        dataIndex: "owner"
                    }, {
                        title: "创建时间",
                        dataIndex: "created",
                    }, {
                        title: "执行耗时",
                        dataIndex: "taken",
                    }, {
                        title: "操作",
                        dataIndex: "id",
                        scopedSlots: {
                            customRender: "detail"
                        }
                    }
                ],
                //需要table与后面的当前页同步
                current: 1,
            }
        },
        computed: {
            pagination: function () {
                return {
                    total: this.$store.state.totalInst,
                    current: this.current,
                };
            },
            instances: function () {
                return this.$store.state.instances;
            },
            currentUser: function () {
                return this.$store.state.username;
            }
        },
        methods: {
            onKill(record) {
                const store = this.$store;
                const that = this;
                const req = {
                    name: record.schedule_name,
                    user: record.owner,
                    id: record.id,
                };
                this.$confirm({
                    title: `强制删除 ${name} ?`,
                    content: '确定要强杀吗',
                    onOk() {
                        let unwatch = store.watch(state => state.killSetMsg,
                            (msg) => {
                                const arr = msg.split("|");
                                const message = arr[1];
                                if (message === "") {
                                    that.$message.info("强杀任务成功", 3);
                                } else {
                                    that.$message.error("强杀任务失败: " + message, 3);
                                }
                                unwatch();
                            }
                        );

                        store.dispatch("KillInstanceAction", req)
                    },
                    onCancel() { },
                });
            },
            advList() {
                this.$store.commit("setCurrentPagInst", 1);
                let req = {
                    search: this.search,
                    from: "search",
                };
                this.$store.dispatch("ListInstanceAction", req);
                this.current = 1;
            },
            onTableChange(e) {
                this.current = e.current;
                this.$store.commit("setCurrentPagInst", e.current);
                let req = {
                    search: this.search,
                    from: "list",
                };
                this.$store.dispatch("ListInstanceAction", req);
            },
            onMenuClick(e) {
                this.$router.push("/" + e.key);
            },
        },
        created() {
            this.$store.dispatch("CurrentUserAction");
            let req = {
                search: this.search,
                from: "created",
            };
            this.$store.dispatch("ListInstanceAction", req);
        }
    }
</script>

<style scoped>
    #top .ant-layout-header {
        background: white;
    }

    #top .ant-layout-content {
        padding: 0 50px;
        margin-top: 64px;
        padding-bottom: 60px;
        background-color: white;
    }

    #top .ant-layout-footer {
        background: white;
        text-align: center;
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 60px;
        clear: both;
    }

    #top .logo {
        width: 240px;
        height: 31px;
        float: left;
        color: white
    }
</style>